<template>
    <div class="confirm-order">
        <div class="top">
            <span class="back-button" @click="goBack">
                <van-icon class="back-icon" name="arrow-left"/>
                <span class="tips">Confirm Order</span>
            </span>
            
        </div>
        <div class="address g-white-card g-card-shadow-gray">
           <div class="left">
               <van-icon name="location"></van-icon>
           </div>
           <div class="center main-area">
                <div class="user-info">
                    <span class="name">David</span>
                    <span class="phone-number">18688888888</span>
                </div>
                <div class="location">
                    湖北省武汉市洪山区珞南街道武汉理工大学鉴湖校区
                </div>


           </div>
           <div class="right icon-button">
               <van-icon name="arrow"></van-icon>
           </div>
        </div>


    </div>
</template>
<script>
export default {
    data(){
        return{
            
        }
    },

    methods:{
        //go back one step, normal situation is going back to cart page
        goBack:function(){
            this.$router.back(1);
        }
    }
}
</script>

<style lang="less" scoped>
    .confirm-order{
        .top{
            margin-top:5px;
            margin-left:5px;
            height:30px;
            line-height: 30px;
            font-size:14px;
            text-align: left;
            .back-button{
                display:flex;
                align-items: center;
                height:100%;
                .van-icon{
                font-size:18px;
                }
                .tips{
                    margin-left:5px;
                }
            }
            
        }


        .address{
            @address-panel-height:80px;
            @address-panel-width:95%;
           


            display: flex;
            justify-content: space-between;
            align-items: center;
            height:@address-panel-height;
            width:@address-panel-width;
            margin-left: (100%-@address-panel-width)/2;
            margin-top:8px;
            padding-top:4px;
            border-radius:8px;
           
           
            .left{
                width:20px;
                height:100%;
                margin-left:3px;
                line-height:@address-panel-height;
                color:@color-red-font;
            }
            .center{
                flex-grow:1;
                height:100%;
                line-height:@address-panel-height;
                text-align: left;
                padding-left:8px;
                .user-info{
                    height:@address-panel-height * 1/3;
                    line-height: @address-panel-height * 1/3;
                    .name{
                        font-size:14px;
                    }
                    .phone-number{
                        font-size:12px;
                        color:@color-gray-font;
                    }
                }
                .location{
                    font-size:12px;
                    height:@address-panel-height * 2/3;
                    line-height: @address-panel-height * 2/3/3;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    
                }
               
            }
            .right{
                width:20px;
                height:100%;
                color:@color-gray-font;
                line-height:@address-panel-height;
            }
        }
      
    }


</style>



